  }
}
@media (min-width: 1200px) {
  .main-nav--opened .main-nav__wrapper-desktop {
    display: flex;
    margin: 0 auto;
    width: 940px;
    justify-content: space-between;
    padding-top: 26px;
  }
  .main-nav--opened .main-nav__list {
    width: 622px;
  }
}
.main-nav--closed .main-nav__wrapper {
  display: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
  border-radius: 1px;
  z-index: 1;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 8px 0 0 #d22856, 0 16px 0 0 #d22856;
  border-radius: 1px;
  z-index: 1;
}
.main-nav--closed .main-nav__toggle:hover:active {
  opacity: 0.3;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(#d22856, 30%);
  box-shadow: 0 8px 0 0 fade(#d22856, 30%), 0 16px 0 0 fade(#d22856, 30%);
  border-radius: 1px;
  z-index: 1;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 11px;
  left: 19px;
  width: 31px;
  height: 3px;
  background-color: #ffffff;
  z-index: 1;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.3;
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:hover::before, .main-nav--opened .main-nav__toggle:hover::after {
  background-color: #d22856;
}

