  position: absolute;
  top: 4px;
  left: 7px;
  content: url("../img/check-icon.svg");
}

.achievement__control-block[type=radio] + .achievement__mark {
  position: absolute;
  top: -7px;
  left: -4px;
  width: 37px;
  height: 37px;
  border-radius: 50%;
  background-color: #ffffff;
}
